<template>
  <div>
    <pb-card class="layout">
      <pb-title class="guild-list-title">
        <span>参数配置</span>
        <pb-button size="small" @click="toggleConfigBox">{{configBoxShow? '收起来':'展开来'}}</pb-button>
      </pb-title>
      <el-collapse-transition>
        <div v-show="configBoxShow">
          <form>
            <el-row style="margin-top: 20px" type="flex" class="row-bg" justify="space-between">
              <span>选择是否包括auto刀</span>
            </el-row>
            <div style="margin-top: 5px">
              <el-radio v-model="is_auto" label="1" border>auto</el-radio>
              <el-radio v-model="is_auto" label="0" border>不auto</el-radio>
            </div>

            <el-row style="margin-top: 20px" type="flex" class="row-bg" justify="space-between">
              <span>选择要打谁</span>
              <el-checkbox :indeterminate="bossesIsIndeterminate" v-model="bossesCheckAll"
                           @change="bossesHandleCheckAllChange">全选
              </el-checkbox>
            </el-row>
            <div style="margin-top: 5px">
              <el-checkbox-group v-model="checkedBosses" @change="bossesHandleCheckedBossesChange" size="small">
                <el-checkbox-button v-for="boss in bossesNum1To4()" :label="boss" :key="boss" class="checkbox-row4">
                  {{boss}}
                </el-checkbox-button>
              </el-checkbox-group>
            </div>
            <div style="margin-top: 5px">
              <el-checkbox-group v-model="checkedBosses" @change="bossesHandleCheckedBossesChange" size="small">
                <el-checkbox-button v-for="boss in bossesNum5()" :label="boss" :key="boss" class="checkbox-row2">
                  {{boss}}
                </el-checkbox-button>
              </el-checkbox-group>
            </div>

            <el-row style="margin-top: 20px" type="flex" class="row-bg" justify="space-between">
              <span>选择周目</span>
              <el-checkbox :indeterminate="cyclesIsIndeterminate" v-model="cyclesCheckAll"
                           @change="cyclesHandleCheckAllChange">全选
              </el-checkbox>
            </el-row>
            <div style="margin-top: 5px">
              <el-checkbox-group v-model="checkedCycles" @change="cyclesHandleCheckedBossesChange" size="small">
                <el-checkbox-button v-for="cycle in cycles" :label="cycle" :key="cycle" class="checkbox-row4">{{cycle}}
                </el-checkbox-button>
              </el-checkbox-group>
            </div>
            <el-button style="margin-top: 30px" type="primary" @click="onSubmit">搜索</el-button>
          </form>
        </div>
      </el-collapse-transition>
    </pb-card>

    <pb-card class="layout" style="margin-top: 30px;">
      <pb-title class="guild-list-title">
        <div>
          <span>队伍组合推荐</span>
          <i class="el-icon-info" @click="showHelpInfo"></i>
        </div>
        <pb-button size="small" @click="nextSquad">换一换</pb-button>
      </pb-title>

      <div v-if="activeSquad">
        <div v-for="(group,index) in activeSquad.groups" :key="index">
          <el-divider></el-divider>
          <el-row type="flex" justify="space-between">
            <span><span>预计{{group.expect_injury}}伤害</span></span>
            <div>
              <el-link @click="showGroupInfo(group)">查看<i class="el-icon-view"></i></el-link>
              <span style="margin-right: 6px"></span>
              <el-link @click="addGroupGuarded(group)">拉黑<i class="el-icon-delete"></i></el-link>
            </div>
          </el-row>
          <ul class="group">
            <li v-for="(role,index) in group.role_list.split('|')">
              <img :src="roles.get(role)" @click="addRoleGuarded(role)" :class="{active: checkRoleGuarded(role)}"
                   alt="">
            </li>
          </ul>
        </div>
      </div>
      <el-divider></el-divider>
      <h3>总计：约{{activeSquad? activeSquad.expect_injury:0}}伤害</h3>
    </pb-card>

    <el-drawer
        title="详情"
        :visible.sync="drawer"
        :direction="direction"
        :size="'60%'">
      <div style="padding: 0 10px">
        <el-row :gutter="20">
          <el-col :span="12">
            <div class="grid-content bg-purple">周目：</div>
          </el-col>
          <el-col :span="12">
            <div class="grid-content bg-purple-light">{{activeGroupInfo.cycle}}</div>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="12">
            <div class="grid-content bg-purple">BOOS：</div>
          </el-col>
          <el-col :span="12">
            <div class="grid-content bg-purple-light">{{activeGroupInfo.position}}王</div>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="12">
            <div class="grid-content bg-purple">是否auto：</div>
          </el-col>
          <el-col :span="12">
            <div class="grid-content bg-purple-light">{{activeGroupInfo.is_auto}}</div>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="12">
            <div class="grid-content bg-purple">是否狂暴：</div>
          </el-col>
          <el-col :span="12">
            <div class="grid-content bg-purple-light">{{activeGroupInfo.state}}</div>
          </el-col>
        </el-row>
        <h3 class="mt-30">作业轴：</h3>
        <!--        <p>work:-->
        <!--          "105狗正中ub↵104猫拳小技能后ub↵102连点狼↵0.57连点环奈↵050狗拳正中ub↵048深月断毒↵046连点猫拳↵038狼等深月破甲ub↵035狗正中ub↵030连点环奈↵022连点猫拳↵019狗拳正中ub↵017深月断毒↵016连点狼↵004猫拳环奈小技能后ub↵002狗拳ub"</p>-->
        <div class="mt-30">
          <a :href="activeGroupInfo.href">
            <pb-button size="small">查看来源</pb-button>
          </a>
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
  const bossesOptions = ['一王', '二王', '三王', '四王', '五王普通', '五王狂暴'];
  const bossesToId = new Map();
  bossesToId.set('一王', 1);
  bossesToId.set('二王', 2);
  bossesToId.set('三王', 3);
  bossesToId.set('四王', 4);
  bossesToId.set('五王普通', 5);
  bossesToId.set('五王狂暴', 5);

  const cyclesOptions = ['一周目', '二周目+'];
  const cyclesToId = new Map();
  cyclesToId.set('一周目', 1);
  cyclesToId.set('二周目+', 2);
  export default {
    name: "Index",
    data() {
      return {
        loading: null,

        bossesCheckAll: true,
        checkedBosses: ['一王', '二王', '三王', '四王', '五王普通', '五王狂暴'],
        bosses: bossesOptions,
        bossesIsIndeterminate: false,

        cyclesCheckAll: false,
        checkedCycles: ['二周目+'],
        cycles: cyclesOptions,
        cyclesIsIndeterminate: true,

        drawer: false,
        direction: 'rtl',

        configBoxShow: false,
        is_auto: '1',

        groups: [],
        squads: new Array(3),   // 3表示最多缓存前三组伤害最高的
        activeSquadIndex: 0,
        activeSquad: [],

        roles: new Map(),
        roleGuarded: [],

        groupGuarded: [],

        bossesTable: new Map(),
        activeGroupInfo: {
          cycle: '',
          position: '',
          is_auto: '',
          state: '',
          href: '',
        },
      };
    },
    methods: {
      bossesHandleCheckAllChange(val) {
        this.checkedBosses = val ? bossesOptions : [];
        this.bossesIsIndeterminate = false;
      },
      bossesHandleCheckedBossesChange(value) {
        let checkedCount = value.length;
        this.bossesCheckAll = checkedCount === this.bosses.length;
        this.bossesIsIndeterminate = checkedCount > 0 && checkedCount < this.bosses.length;
      },
      bossesNum1To4() {
        return this.bosses.slice(0, 4);
      },
      bossesNum5() {
        return this.bosses.slice(4, 6);
      },

      cyclesHandleCheckAllChange(val) {
        this.checkedCycles = val ? cyclesOptions : [];
        this.cyclesIsIndeterminate = false;
      },
      cyclesHandleCheckedBossesChange(value) {
        let checkedCount = value.length;
        this.cyclesCheckAll = checkedCount === this.cycles.length;
        this.cyclesIsIndeterminate = checkedCount > 0 && checkedCount < this.cycles.length;
      },

      activeLoading() {
        this.loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
      },

      toggleConfigBox() {
        this.configBoxShow = !this.configBoxShow;
      },

      showHelpInfo() {
        this.$toast('点击头像筛选没有的角色，换一换后会自动重新计算。', "normal");
      },

      onSubmit() {
        this.activeLoading();
        window.setTimeout(() => {
          this.promiseJoinTheSquads();
        }, 100);
      },

      is_autoFilter(groups) {
        groups = groups.filter(item => {
          return item.is_auto == this.is_auto;
        });
        return groups;
      },

      groupGuardedFilter(groups) {
        groups = groups.filter(item => {
          return this.groupGuarded.includes(item.bigfun_id) ? false : true;
        });
        return groups;
      },

      cycleFilter(groups) {
        let checkedCyclesIds = [];
        for (let key in this.checkedCycles) {
          let cycleStr = this.checkedCycles[key];
          checkedCyclesIds.push(cyclesToId.get(cycleStr));
        }
        groups = groups.filter(item => {
          let boss = this.bossesTable.get(item.boss_id);
          return checkedCyclesIds.includes(boss.cycle);
        });
        return groups;
      },

      bossFilter(groups){
        let checkedBossesIds = [];
        for(let key in this.checkedBosses){
          let bossStr = this.checkedBosses[key];
          checkedBossesIds.push(bossesToId.get(bossStr));
        }
        groups = groups.filter(item=>{
          let boss = this.bossesTable.get(item.boss_id);
          return checkedBossesIds.includes(boss.position);
        })
        return groups;
      },

      roleGuardedFilter(groups) {
        let groups_filter = [];
        for (let key in groups) {
          let group = groups[key];
          let role_list = group.role_list.split('|');
          let {unique, repeated} = this.arrUniqueAndRepeatedContent(role_list, this.roleGuarded);
          group.roleGuarded = repeated;
          if (group.roleGuarded.size < 2) groups_filter.push(group);
        }
        return groups_filter;
      },

      rolesById() {
        let roles = JSON.parse(process.env.VUE_APP_ROLES);
        for (let key in roles) {
          let role = roles[key];
          this.roles.set(role.id.toString(), role.avatar);
        }
      },

      bossesById() {
        let bosses = JSON.parse(process.env.VUE_APP_BOSSES);
        for (let key in bosses) {
          let boss = bosses[key];
          this.bossesTable.set(boss.id, boss);
        }
      },

      nextSquad() {
        let localRoleGuarded = this.$store.get('roleGuarded');
        if (localRoleGuarded.length !== this.roleGuarded.length) {
          this.activeLoading();
          window.setTimeout(() => {
            this.promiseJoinTheSquads().then(() => {
              this.$store.set('roleGuarded', this.roleGuarded);
            });
          }, 300);
        } else {
          this.activeSquadIndex = this.activeSquadIndex > 0 ? --this.activeSquadIndex : (this.squads.length - 1);
          this.activeSquad = this.squads[this.activeSquadIndex];
        }
      },

      addRoleGuarded(id) {
        // 搜索是否存在没有的角色列表中了，没有则添加，有则删除
        let indexOf = this.roleGuarded.indexOf(id.toString());
        if (indexOf > -1) {
          this.roleGuarded.splice(indexOf, 1);
        } else {
          this.roleGuarded.push(id);
        }
      },

      addGroupGuarded(group) {
        this.$confirm('是否删除编号：' + group.bigfun_id + '；删除后可在角色页面处还原。', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
        }).then(() => {
          if (!this.groupGuarded.includes(group.id)) {
            this.groupGuarded.push(group.bigfun_id);
            this.$store.set('groupGuarded', this.groupGuarded);
            this.activeLoading();
            window.setTimeout(() => {
              this.promiseJoinTheSquads();
            }, 100);
          }
        }).catch(() => {
          this.$toast('已停止拉黑');
        });

      },

      checkRoleGuarded(id) {
        return this.roleGuarded.indexOf(id.toString()) > -1;
      },

      showGroupInfo(group) {
        let activeGroupInfo = {};
        this.drawer = true;
        let boss = this.bossesTable.get(group.boss_id);
        activeGroupInfo.cycle = boss.cycle;
        activeGroupInfo.position = boss.position;
        activeGroupInfo.state = boss.state;
        activeGroupInfo.href = 'https://www.bigfun.cn/tools/pcrteam/works/' + group.bigfun_id;
        activeGroupInfo.is_auto = group.is_auto ? '是' : '否';
        this.activeGroupInfo = activeGroupInfo;
      },

      promiseJoinTheSquads() {
        return new Promise((resolve, reject) => {
          try {
            this.squads = new Array(3);
            this.joinTheSquads();
            resolve('成功');
          } catch (e) {
            reject('失败');
          }
        }).then((res) => {
          this.$toast(res, 'success');
          this.loading.close();

        }).catch((err) => {
          this.$toast(err, 'danger');
          this.loading.close();
        });
      },

      joinTheSquads() {
        // 初始化队伍
        this.groups = JSON.parse(process.env.VUE_APP_GROUPS);
        let groups = this.groups;

        // auto刀过滤
        groups = this.is_autoFilter(groups);

        // 黑名单过滤
        groups = this.groupGuardedFilter(groups);

        // 周目过滤
        groups = this.cycleFilter(groups);

        // Bosee过滤
        groups = this.bossFilter(groups);

        // 缺角色过滤
        //   缺角色数量>=2 直接去除
        //   缺角色数量<2  保留并标记缺少的数量。
        groups = this.roleGuardedFilter(groups);

        // 队伍过滤，保留this.squads数组长度数量队伍
        for (let i = 0; i < groups.length; i++) {
          if (!groups[i + 1]) continue;
          for (let j = i + 1; j < groups.length; j++) {
            // 转换前两支队伍
            let group1 = groups[i];
            let group2 = groups[j];
            let role_list1 = group1.role_list.split('|');
            let role_list2 = group2.role_list.split('|');
            // 如果两个队伍三人相同就直接跳过
            let {unique, repeated} = this.arrUniqueAndRepeatedContent(role_list1, role_list2);
            if (repeated.size >= 3) continue;

            if (!groups[j + 1]) continue;
            for (let k = j + 1; k < groups.length; k++) {
              // 转换第三支队伍情况
              let group3 = groups[k];
              let role_list3 = group3.role_list.split('|');

              // 就算借满三个角色，也至少需要12名独立的角色
              let {unique, repeated} = this.arrUniqueAndRepeatedContent(role_list1, role_list2, role_list3);
              if (unique.size < 12) continue;

              // 仲間いる: 计算每个队伍中，几名角色属于重复出现的角色（要借的）
              let group1_copy = {...group1};
              group1_copy.nakamaIru = repeatedRole_count.call(this, role_list1, repeated);
              // 每个队伍最多两名角色在其它队伍也重复出现，例如狼狗互借
              if (group1_copy.nakamaIru > 2) continue;

              // 出现需要借角色的情况，不能有任何角色处于黑名单。
              if (group1_copy.nakamaIru > 0 && group1_copy.roleGuarded.size > 0) continue;
              // 不需要借角色的情况，最多可以有一名黑名单。
              if (group1_copy.nakamaIru == 0 && group1_copy.roleGuarded.size > 1) continue;

              let group2_copy = {...group2};
              group2_copy.nakamaIru = repeatedRole_count.call(this, role_list2, repeated);
              if (group2_copy.nakamaIru > 2) continue;
              if (group2_copy.nakamaIru > 0 && group2_copy.roleGuarded.size > 0) continue;
              if (group2_copy.nakamaIru == 0 && group2_copy.roleGuarded.size > 1) continue;


              let group3_copy = {...group3};
              group3_copy.nakamaIru = repeatedRole_count.call(this, role_list3, repeated);
              if (group3_copy.nakamaIru > 2) continue;
              if (group3_copy.nakamaIru > 0 && group3_copy.roleGuarded.size > 0) continue;
              if (group3_copy.nakamaIru == 0 && group3_copy.roleGuarded.size > 1) continue;

              // 总伤害排在前三内，则插入对应位置
              let squad = {
                groups: [
                  group1_copy,
                  group2_copy,
                  group3_copy,
                ],
                expect_injury: 0
              };
              squad = sumSquadExpect_injury(squad);
              let index = null;
              for (let e = this.squads.length - 1; e >= 0; e--) {
                if (this.squads[e] == undefined) {
                  index = e;
                  continue;
                }
                if (squad.expect_injury > this.squads[e].expect_injury) index = e;
              }
              if (index !== null) this.squads.splice(index, 1, squad);
            }
          }
        }

        // 显示第一支队伍
        this.activeSquad = this.squads[0];

        this.$forceUpdate();

        // 返回该队重复出现角色的数量
        function repeatedRole_count(role_list, repeated_list) {
          let {unique, repeated} = this.arrUniqueAndRepeatedContent(role_list, repeated_list);
          return repeated.size
        }

        // 返回squad伤害总和
        function sumSquadExpect_injury(squad) {
          for (let key in squad.groups) {
            squad.expect_injury += squad.groups[key].expect_injury;
          }
          return squad;
        }
      },
    },

    created() {
      this.roleGuarded = this.$store.get('roleGuarded') ? this.$store.get('roleGuarded') : [];
      this.groupGuarded = this.$store.get('groupGuarded') ? this.$store.get('groupGuarded') : [];
      this.rolesById();
      this.bossesById();
      this.joinTheSquads();
    }
  };
</script>

<style>
  .guild-list {
    display: flex;
  }

  .guild-list-title {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }

  .checkbox-row4 {
    width: 25%;
  }

  .checkbox-row4 span {
    width: 100%;
  }

  .checkbox-row2 {
    width: 50%;
  }

  .checkbox-row2 span {
    width: 100%;
  }

  .group, .group > li {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  .group {
    display: flex;
    margin: 5px 0;
  }

  .group > li {
    width: 20%;
    padding: 0 3px;
  }

  .group > li > img {
    display: block;
    width: 100%;
    max-width: 66px;
  }

  .group > li > img.active {
    border: 2px solid #f55291;
    border-radius: 3px;
  }

  div.el-divider {
    margin: 10px;
  }


</style>