<template>
  <div>
    <pb-card class="layout">
      <pb-title class="guild-list-title">
        <span>未拥有的角色</span>
        <pb-button size="small" @click="toggleRoleConfigBox">{{configRoleBoxShow? '收起来':'展开来'}}</pb-button>
      </pb-title>
      <el-collapse-transition>
        <div class="roles" style="margin-top: 20px" v-show="configRoleBoxShow">

          <div class="role" v-for="role in roleGuarded">
            <div class="el-badge">
              <el-image class="role-head" :src="roles.get(role)"></el-image>
              <sup class="el-badge__content el-badge__content--undefined is-fixed el-icon-close"
                   @click="removeRole(role)"></sup>
            </div>
          </div>

        </div>
      </el-collapse-transition>
    </pb-card>
    <pb-card class="layout mt-30">
      <pb-title class="guild-list-title">
        <span>黑名单队伍</span>
        <pb-button size="small" @click="toggleGroupConfigBox">{{configGroupBoxShow? '收起来':'展开来'}}</pb-button>
      </pb-title>
      <el-collapse-transition>
        <div class="group" style="margin-top: 20px" v-show="configGroupBoxShow">
          <ul class="group-list">
            <li v-for="group in groupGuarded">
              <span>id: {{group}}</span>
              <div>
                <el-link :href="'https://www.bigfun.cn/tools/pcrteam/works/'+group">查看详情<i class="el-icon-view"></i>
                </el-link>
                <span style="margin-right: 6px"></span>
                <el-link @click="removeGroup(group)">还原队伍<i class="el-icon-refresh-left"></i></el-link>
              </div>
            </li>
          </ul>
        </div>
      </el-collapse-transition>
    </pb-card>
  </div>
</template>

<script>
  export default {
    name: "role",
    data() {
      return {
        src: 'https://i0.hdslb.com/bfs/bigfun/aed557492582edec629ed6bceb2157d6ee06c930.png',
        configRoleBoxShow: true,
        roles: new Map(),
        roleGuarded: [],

        configGroupBoxShow: true,
        groupGuarded: [],
      }
    },
    methods: {
      toggleRoleConfigBox() {
        this.configRoleBoxShow = !this.configRoleBoxShow;
      },
      toggleGroupConfigBox() {
        this.configGroupBoxShow = !this.configGroupBoxShow;
      },
      rolesById() {
        let roles = JSON.parse(process.env.VUE_APP_ROLES);
        for (let key in roles) {
          let role = roles[key];
          this.roles.set(role.id.toString(), role.avatar);
        }
      },
      removeRole(id) {
        let indexOf = this.roleGuarded.indexOf(id.toString());
        if (indexOf > -1) {
          this.roleGuarded.splice(indexOf, 1);
        } else {
          this.roleGuarded.push(id);
        }
        this.$store.set('roleGuarded', this.roleGuarded);
      },

      removeGroup(id) {
        let indexOf = this.groupGuarded.indexOf(id);
        if (indexOf > -1) {
          this.groupGuarded.splice(indexOf, 1);
        }
        this.$store.set('groupGuarded', this.groupGuarded);
      }
    },
    created() {
      this.roleGuarded = this.$store.get('roleGuarded');
      this.groupGuarded = this.$store.get('groupGuarded');
      this.rolesById();
    }
  }
</script>

<style scoped>
  .roles {
    display: flex;
    flex-wrap: wrap;
  }

  .role {
    width: 19%;
    margin: 3%;
  }

  .role-head {
    width: 50px;
    height: 50px;
  }

  .el-badge__content {
    background-color: #F56C6C;
    border-radius: 50%;
    color: #FFF;
    display: inline-block;
    font-size: 16px;
    height: 18px;
    line-height: 18px;
    padding: 3px 4px;
    text-align: center;
    white-space: nowrap;
    border: 1px solid #FFF;
  }

  .group-list {
    width: 100%;
    list-style: none;
  }

  .group-list > li {
    display: flex;
    justify-content: space-between;
  }
</style>