/**
 * 数组去重
 * @param arr
 * @returns {any[]}
 */
function arrUnique(arr) {
  return Array.from(new Set(arr));
}

/**
 * 返回多个数组之间相同和不同（至少出现一次）的部分
 * 例如[1,3,5,2]和[1,2,3,4,5]
 * 返回[1,2,3,4,5]和[1,2,3,5]
 * @param array
 * @returns {{unique: Set<any>, repeated: Set<any>}}
 */
function arrUniqueAndRepeatedContent(...array){
  let unique = new Set();
  let repeated = new Set();
  array.forEach(arr => {
    arr.forEach(value => {
      if(!unique.has(value)){
        unique.add(value);
      }else{
        repeated.add(value);
      }
    });
  });
  return {
    unique,
    repeated
  }
}

export default {
  install(Vue) {
    Vue.prototype.arrUnique = arrUnique;
    Vue.prototype.arrUniqueAndRepeatedContent = arrUniqueAndRepeatedContent;
  },
};