/**
 * Yos-Axios Plugin
 */
import Axios from "axios";
import errors from "./error";
import store from "../../store";

function create(options) {
  const axios = Axios.create(options);

  axios.interceptors.request.use((config) => {
    const headers = {
      ...config.headers,
      Authorization: "Bearer " + localStorage.access_token,
    };

    config.headers = headers;
    return config;
  });

  axios.interceptors.response.use(
    (response) => {
      if (response.data.code === 200) {
        return response;
      } else {
        let error = new Error("未知请求错误");
        // console.log(errors[response.config.url][response.data.error]);
        try {
          if (!errors[response.config.url][response.data.error]) {
            throw "orz";
          }
          error = new Error(errors[response.config.url][response.data.error]);
        } catch (e) {
          error = new Error("获取不到错误信息");
          if (response.data.msg) {
            error = new Error('Error code'+response.data.code + ': ' + response.data.msg);
          }
        }

        return Promise.reject(error);
      }
    },
    (err) => {
      let error = new Error("未知错误");
      // console.log(err);
      return Promise.reject(err);
    }
  );

  return axios;
}

export default {
  install(Vue) {
    const URL = process.env.VUE_APP_API;
    Vue.prototype.$api = create({baseURL: URL}, Vue);
  },
};
