<template>
  <div class="pb-toast" :data-type="type" @animationend="onAnimationend">
    <img class="pb-toast-img" :src="img_url" alt="img" />
    <div class="toast-content">
      <slot>No Message</slot>
    </div>
  </div>
</template>

<script>
  /**
   * type: danger|normal|success
   *
   * */
export default {
  inheritAttrs: false,
  name: "PbToast",
  props: {
    type: {
      type: String,
      default: "normal"
    }
  },
  computed: {
    img_url() {
      return require("../../assets/toast/" +
        this.type +
        // "_" +
        Math.floor(Math.random() * 3) +
        ".png");
    }
  },
  methods:{
      onAnimationend(e){
        this.$emit("animationend",e);
      }
  }
};
</script>

<style scoped>
@keyframes toast {
  0% {
    transform: translateY(calc(100% + 100px));
    opacity: 0;
  }
  20% {
    transform: translateY(0px);
    opacity: 1;
  }
  80% {
    transform: translateY(0px);
    opacity: 1;
  }
  100% {
    transform: translateY(calc(100% + 100px));
    opacity: 0;
  }
}

.pb-toast {
  background: #fff;
  width: 240px;
  padding: 30px;
  position: absolute;
  bottom: 0;
  animation: toast 2s forwards;
}

.pb-toast-img {
  object-fit: cover;
  width: 80px;
  height: 80px;
  position: absolute;
  right: -45px;
  top: -45px;
}

.toast-content {
  word-break: break-all;
}

.pb-toast[data-type="danger"] {
  border-top: 4px solid #ff66a1;
}

.pb-toast[data-type="normal"] {
  border-top: 4px solid #1987e6;
}

.pb-toast[data-type="success"] {
  border-top: 4px solid #6affb7;
}
</style>