<template>
  <div class="layout login-layout">
    <pb-card class="login-card">
      <pb-title>登录</pb-title>
      <form class="mt-30">
        <!-- Inputs List -->
        <div class="form-input">
          <div class="input-group">
            <input
                type="text"
                id="username"
                placeholder="请输入用户名"
                v-model="info.username"
                class="login-input"
                autocomplete="off"
            />
            <label for="username" class="input-label">用户名</label>
            <label class="input-left"></label>
          </div>
          <div class="input-group">
            <input
                type="password"
                id="password"
                placeholder="请输入密码"
                v-model="info.password"
                class="login-input"
            />
            <label for="password" class="input-label">密码</label>
            <label class="input-left"></label>
          </div>
        </div>
        <!-- Links List -->
        <div class="login-bottom-link">
          <router-link class="lb-link" to="#">忘记密码？</router-link>
        </div>
        <!-- Buttons List -->
        <div class="mt-30">
          <pb-button size="large" @click="formSubmit" style="margin-right:20px">登录</pb-button>
          <pb-button size="large" type="pink" @click="$router.push('/register')">注册</pb-button>
        </div>
      </form>
    </pb-card>
  </div>
</template>

<script>
  import PbCard from "@/components/PbUi/PbCard";
  import PbTitle from "@/components/PbUi/PbTitle";
  import PbInput from "@/components/PbUi/PbInput";
  import PbButton from "@/components/PbUi/PbButton";

  export default {
    name: "Login",
    components: {
      PbCard,
      PbInput,
      PbTitle,
      PbButton
    },
    data() {
      return {
        info: {
          username: "",
          password: ""
        }
      };
    },
    methods: {
      formSubmit(e) {
        // console.log(this.$api);
        this.$api.post('/login', this.info).then(val => {
          let data = val.data.data;
          localStorage.access_token = data.token;
          this.$router.push("/");
        })
          .catch(err => {
            this.$toast(err.message, "danger");
          })
      }
    },
  };
</script>

