  .login-input:focus ~ .input-label {
    color: #1385e5;
  }

  .input-group {
    position: relative;
    padding: 12px 15px;
    border: 1px solid rgba(0, 0, 0, 0.2);
  }

  .input-group:not(:last-child) {
