<template>
  <div v-bind="$attrs">
    <div class="file-upload-btn" @click="fileInputClick">
      <template v-if="!file">
        <PbIcon fill="#ddd" />
      </template>
      <template v-else>
        <video class="preview-video" :src="video_url" muted></video>
        
      </template>
    </div>
    <input
      type="file"
      :name="name"
      id="file"
      :data-id="id"
      style="display:none"
      @change="fileChange"
      accept="video/*"
    />
  </div>
</template>

<script>
import PbIcon from "@/components/PbIcon/Icon";

export default {
  inheritAttrs: false,
  name: "PbFileInput",
  components: {
    PbIcon
  },
  props: {
    name: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      id: Math.random(),
      file: null
    };
  },
  computed: {
    video_url() {
      return this.file ? URL.createObjectURL(this.file) : null;
    }
  },
  methods: {
    fileInputClick() {
      this.$el.querySelector("input").click();
    },
    fileChange(e) {
      if (e.target.files.length) {
        this.file = e.target.files[0];
      } else {
        this.file = null;
      }
    }
  }
};
</script>

<style scoped>
.file-upload-btn {
  cursor: pointer;
  width: 200px;
  height: 140px;
  background: rgba(255, 255, 255, 1);
  display: flex;
  justify-content: center;
  align-items: center;
}

.file-upload-btn >>> .pb-icon {
  width: 40%;
  height: 40%;
}

.preview-video {
  width: 100%;
}
</style>