import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import data from "./store/data";
import userCredential from "./plugins/userCredential";
import axiosApi from "./plugins/axiosApi";
import PbUi from "./plugins/PbUi";
import common from "./plugins/commonFunction/common";
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

Vue.use(store);

Vue.use(userCredential);

Vue.use(axiosApi);

Vue.use(PbUi);

Vue.use(common);

Vue.use(ElementUI);

Vue.config.productionTip = false;

Vue.prototype.$toast = function(content = "", type = "normal") {
  this.$root.toasts.push({ type: type, content: content });
};

new Vue({
  router,
  data,
  render: (h) => h(App),
  mounted() {},
}).$mount("#app");
