<template>
  <button
    class="pb-button"
    :type="[ ( 'submit' in this.$attrs ) ? 'submit' : 'button' ]"
    @click="onClick"
    :data-size="size"
    :data-type="type"
  >
    <slot></slot>
  </button>
</template>

<script>
export default {
  inheritAttrs:false,
  name: "PbButton",
  props: {
    size: {
      type: String,
      default: ""
    },
    type: {
      type: String,
      default: ""
    }
  },
  data() {
    return {};
  },
  methods: {
    onClick(e) {
      this.$emit("click", e);
    }
  }
};
</script>

