  padding: 14px 24px;
}

.pb-button[data-size="small"] {
  padding: 8px 14px;
  font-size: 0.75rem;
  letter-spacing: 2px;
  text-indent: 2px;
}

.pb-button[data-type="pink"] {
  background: #f55291;
}

.pb-button[data-type="pink"]:hover {
