let store = {
  storageKey:'pcr_hub',
  data:{
    user:{},
    token:'',
    roleGuarded:[],
  },
  set(key,value){
    this.data[key] = value;
    localStorage.setItem(this.storageKey,JSON.stringify(this.data));
  },
  get(key){
    if(!localStorage.getItem(this.storageKey))localStorage.setItem(this.storageKey,JSON.stringify(this.data));
    return JSON.parse(localStorage.getItem(this.storageKey))[key];
  },
  remove(){  //  删除
    localStorage.removeItem(this.storageKey);
  }
};
export default {
  install(Vue) {
    Vue.prototype.$store = store;
  },
};
