import Vue from "vue";
import VueRouter from "vue-router";
import Index from "../pages/Index";
import Login from "../pages/Login";
import Register from "../pages/Register";
import Guild from "../pages/Guild";
import Role from "../pages/Role";

Vue.use(VueRouter);


const routes = [
  {
    path: '',
    redirect: '/index',
  },
  {
    path: '/',
    redirect: '/index',
  },
  {
    path: "/index",
    name: "index",
    component: Index,
  },
  {
    path: "/login",
    name: "login",
    component: Login,
  },
  {
    path: "/register",
    name: "register",
    component: Register,
  },
  {
    path: "/role",
    name: "role",
    component: Role,
  },
  {
    path: "/guild/:id",
    name: "guild",
    component: Guild
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.NODE_ENV === "development" ? "/" : "/pcr_hub",
  routes,
});

router.afterEach((to, from) => {
  // console.log(to);
});

router.beforeEach((to, from, next) => {
  /*
    let auth = router.app.$credentialLoad();
    if (!auth) {
      //未登陆，访问登陆注册外的
      if (!auth && (to.name != 'login' && to.name != 'register')) {
        next({name: 'login'});
      } else {
        next();
      }
    } else {
      //已登陆
      next();
    }
  */
  next();
});

export default router;
